<script>
import { mapGetters } from "vuex";

export default {
  name: "Details",
  data() {
    return {
      finishTime: "",
      future: +new Date("2024-11-30 23:59:59"),
      total: "",
      countTimer: null,
    };
  },
  mounted() {
    // this.$store.dispatch("details/getNewList");
    // sessionStorage.setItem("NEWLISTS", JSON.stringify(this.newLists));
    this.$store.dispatch("details/getTimes");

    // Vue
    // this.intervalld = setInterval(() => {
    //   this.$forceUpdate();
    // }, 100);

    this.changeTime();
    this.countTimer = setInterval(() => {
      this.changeTime();
      // console.log(111);
    }, 1000);
  },
  //  
  beforeDestroy() {
    if (this.countTimer) {
      clearInterval(this.countTimer);
      this.countTimer = null;
    }
  },
  methods: {
    changeTime() {
      const now = Date.now();
      this.total = parseInt(this.future - now) / 1000;
      this.finishTime = this.finishTimes;
    },
    goApply() {
      this.$router.push({
        path: "/apply",
      });
    },
  },
  computed: {
    // ...mapGetters("details", ["newLists"]),
    ...mapGetters("details", ["gameTimes"]),
    finishTimes() {
      // 
      // ( - ) / 1000  
      // d=parseInt(/60/60/24)  
      // h=parseInt(/60/60%24)  
      // m=parseInt(/60%60)  
      // s=parseInt(%60)  
      // parseInt()  
      // const date = new Date();

      // Date.now()  
      // console.log("", Date.now());
      // const now = Date.now();

      //  +new Date()
      // console.log(+new Date("2024-11-30 23:59:59"));
      // const future = ;

      // 
      // const total = parseInt(this.future - now) / 1000;

      if (this.total < 0) {
        return "";
      }
      // 
      let d = parseInt(this.total / 60 / 60 / 24);
      // 
      let h = parseInt((this.total / 60 / 60) % 24);
      h = h < 10 ? "0" + h : h;
      // 
      let m = parseInt((this.total / 60) % 60);
      m = m < 10 ? "0" + m : m;
      // 
      let s = parseInt(this.total % 60);
      s = s < 10 ? "0" + s : s;

      let day = `${d}${h}:${m}:${s}`;
      return day;
    },
  },
  // watch: {
  //   finishTime: {
  //     handler(newValue, oldValue) {
  //       this.finishTime = newValue;
  //       console.log(111111);
