// 
import { reqNewList, reqTimes } from '@/api/index'
// 

// 
const state = {
  newLists: [],
  gameTimes: {}
}

// 
const actions = {
  // 
  async getNewList(context) {
    const result = await reqNewList()
    if (result.code == 200) {
      context.commit('GETNEWLIST', result.data)
    }
  },

  // 
  async getTimes(context) {
    const result = await reqTimes()
    if (result.code == 200) {
      context.commit('GETTIMES', result.data)
    }
  }
}

// state
const mutations = {
  GETNEWLIST(state, value) {
    state.newLists = value
  },

  GETTIMES(state, value) {
    state.gameTimes = value
  }
}

// state
const getters = {
  newLists(state) {
    return state.newLists
  },

  gameTimes(state) {
    return state.gameTimes
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}